import { connect } from "react-redux";
import Select      from "react-select";

//Our Importations
import { bindAll } from "Helpers";
import { SortDes } from "Resources/icons";
import HeaderPage  from "Components/Global/HeaderPage";
import UsersSlice  from "Store/Slices/UsersSlice";
import UserCard    from "Components/Users/UserCard";
import Paginate    from "Components/Global/Paginate";
import ModalUsers  from "Components/Users/ModalUsers";
import "./UserPage.scss";

const status =[
	{
		value : true,
		label : "Activo",
	},
	{
		value : false,
		label : "Inactivo",
	},
];

const roles = [
	{
		label : "Administrador",
		value : "administrator",
	},
	{
		label : "Consulta",
		value : "consult",
	},
	{
		label : "Invitado",
		value : "guest",
	},
	{
		label : "Perito",
		value : "perit",
	},
	{
		label : "Administrador  Técnico",
		value : "administrator_technician",
	},
];

const sort = [
	{
		value : "name",
		label : "Nombre",
	},
	{
		value : "lastname",
		label : "Apellido",
	},
	{
		value : "active",
		label : "Status",
	},
];

const validity = [
	{
		value : "validity",
		label : "Vigente",
	},
	{
		value : "almost_expired",
		label : "Por Vencer",
	},
	{
		value : "expired",
		label : "Vencido",
	},
];

const UsersPage = ({
	users,
	totalPages,
	usersSliceActions,
	delegations : {
		handleSearch,
		handleFilters,
		statesOptions,
		handleChangeFilter,
	},
}) => {
	return (
		<div id="UserPage">
			<ModalUsers rolesOptions={roles} statesOptions={statesOptions} />
			<HeaderPage
				title="Usuarios"
				btnLabel="Agregar Usuario"
				handleOnclick={() => usersSliceActions.openCreateUserModal()}
				handleSearch={handleSearch}
			/>
			<div className="wrapper-filters">
				<div className="filters-container mb-2">
					<Select
						isMulti
						isClearable
						options={roles}
						placeholder="Rol"
						classNamePrefix="select"
						onChange={(e) => handleChangeFilter("roles", e)}
						menuPortalTarget={document.body}
					/>
					<Select
						isMulti
						isClearable
						options={statesOptions}
						placeholder="Estados"
						classNamePrefix="select"
						onChange={(e) => handleChangeFilter("states", e)}
						menuPortalTarget={document.body}
					/>
					<Select
						isClearable
						options={status}
						placeholder="Status"
						classNamePrefix="select"
						onChange={(e) => handleChangeFilter("status", e)}
						menuPortalTarget={document.body}
					/>
					<Select
						isMulti
						isClearable
						options={validity}
						placeholder="Vigencia"
						classNamePrefix="select"
						onChange={(e) => handleChangeFilter("validities", e)}
						menuPortalTarget={document.body}
					/>
				</div>

				<div className="wrapper-sort">
					<div className="group">
						<SortDes />
						<p>Organizar por:</p>
					</div>
					<div>
						<Select
							isClearable
							options={sort}
							classNamePrefix="select"
							className="search-select"
							onChange={(e) => handleChangeFilter("orderBy", e)}
							menuPortalTarget={document.body}
						/>
					</div>

				</div>
			</div>
			<div className="cards-container mb-1">
				{ users.map(user => <UserCard key={user._id}  user={user} /> ) }
			</div>
			<Paginate
				module="users"
				totalPage={totalPages}
				actionType={"getAllUsers"}
				handleGetData={handleFilters}
			/>
		</div>
	);
};

const mapDispatchToState = ({ _usersSlice }) => ({
	users      : _usersSlice?.docs       ?? [],
	totalPages : _usersSlice?.totalPages ?? 1,
});

const mapDispatchToProps = bindAll({ UsersSliceActions : UsersSlice.actions });

export default connect(mapDispatchToState, mapDispatchToProps)(UsersPage);
