import { connect } from "react-redux";
import {
	Mail,
	Phone,
	Clock,
	FileMinus,
} from "react-feather";
import {
	Card,
	Badge,
	CardBody,
	DropdownMenu,
	DropdownItem,
	ButtonDropdown,
	DropdownToggle,
	UncontrolledTooltip,
} from "reactstrap";

//Import Own Components
import { bindAll }    from "Helpers";
import { ModalSlice } from "Store/Slices";
import { Menu }       from "Resources/icons";
import UsersSlice     from "Store/Slices/UsersSlice";
import UserImage      from "Components/Global/UserImage";
import "./UserCard.scss";

const roles = {
	perit                    : "Perito",
	consult                  : "Consulta",
	guest                    : "Invitado",
	assistant                : "Asistente",
	administrator            : "Administrador",
	administrator_technician : "Administrador Técnico",
};


const UserCard = ({
	user,
	modalSliceActions,
	usersSliceActions,
	delegations : {
		isExpired,
		dropdownOpen,
		toggleDropdown,
		handleShowUserDetail,
	},
}) => {

	return (
		<div id="UserCard">
			<Card id="card">
				<CardBody className="click-card" onClick={() => handleShowUserDetail(user?._id)}>
					{
						<div className="icons-wrapper ">
							{
								isExpired(user?.countDays) === 0 &&
								<>
									<Clock  id={"almost-expired" + user?._id} className="clock-color" size={15} />
									<UncontrolledTooltip  target={"almost-expired" + user?._id}>
										La vigencia del registro está apunto de expirar
									</UncontrolledTooltip>
								</>
							}
							{
								isExpired(user?.countDays) === 1 &&
								<>
									<Clock id={"almost-expired" + user?._id} className="alert-color" size={15} />
									<UncontrolledTooltip  target={"almost-expired" + user?._id}>
										La vigencia del registro ha expirado
									</UncontrolledTooltip>
								</>
							}
							{
								!user?.complete_profile &&
								<>
									<FileMinus id={"files" + user?._id} className="alert-trinagle-color" size={14} />
									<UncontrolledTooltip  target={"files" + user?._id }>
										Falta documentación
									</UncontrolledTooltip>
								</>
							}
						</div>
					}
					<div className="card-header-style">
						<UserImage
							image={user?.image}
							name={user?.name}
							subIcon={true}
							subIconColor={user?.active ? "#52658f" : "#E5E5E5"}
						/>
						<div>
							<Badge className="badge-style admin">
								{roles?.[user?.role] ?? user?.role}
							</Badge>
							<ButtonDropdown direction='start' isOpen={dropdownOpen} toggle={toggleDropdown}>
								<DropdownToggle
									size="sm"
									color="flat-secondary"
									onClick={toggleDropdown}
									className="btn-icon rounded-circle"
								>
									<Menu size={20} />
								</DropdownToggle>
								<DropdownMenu>
									<DropdownItem
										tag='a'
										onClick={() => usersSliceActions.openCreateUserModal(user?._id )}
									>
										Editar
									</DropdownItem>
									{
										user?.active ?
											<DropdownItem
												tag="a"
												onClick={() =>
													modalSliceActions.openModal({
														type  : "confirmationModal",
														props : {
															image                : "",
															_id                  : user?._id,
															name                 : "Usuario",
															identifier           : user?.email,
															actionToDispatch     : "disableUser",
															title                : "Deshabilitar Usuario",
															confirmationQuestion : "¿Estás seguro que quieres deshabilitar este usuario?",
															bodyText             : "Podrás volver a habilitar al usuario en cualquier momento.",
															confirmLabel         : "Sí, Deshabilitar",
														},
													})}
											>
												Deshabilitar
											</DropdownItem>
											:
											<DropdownItem
												tag="a"
												onClick={() =>
													modalSliceActions.openModal({
														type  : "confirmationModal",
														props : {
															image                : "",
															_id                  : user?._id,
															name                 : "Usuario",
															identifier           : user?.email,
															actionToDispatch     : "enableUser",
															title                : "Habilitar Usuario",
															confirmationQuestion : "¿Estás seguro que quieres habilitar este usuario?",
															bodyText             : "Podrás volver a deshabilitar al usuario en cualquier momento.",
															confirmLabel         : "Sí, Habilitar",
														},
													})}
											>
												Habilitar
											</DropdownItem>
									}
									<DropdownItem
										tag="a"
										onClick={() =>
											modalSliceActions.openModal({
												type  : "confirmationModal",
												props : {
													image                : "",
													_id                  : user?._id,
													identifier           : user?.email,
													name                 : "Usuario",
													actionToDispatch     : "deleteUser",
													title                : "Eliminar Usuario",
													confirmationQuestion : "¿Estás seguro que quieres eliminar este usuario?",
													bodyText             : "Este usuario se eliminará permanentemente.",
													confirmLabel         : "Sí, Eliminar",
												},
											})}
									>
										Eliminar
									</DropdownItem>
								</DropdownMenu>
							</ButtonDropdown>
						</div>
					</div>
					<div>
						<p className="name-text mt-1">{user?.name} {user?.lastname}</p>
						<p className="user-name-text">{user?.username}</p>
						<div className="content-group mt-2">
							<div className="group-icon-text">
								<Mail className="icon-color" size={15} />
								<p className="label">Correo: </p>
							</div>
							<p className="text">{user?.email ? user.email: "---"}</p>
						</div>
						<div className="content-group mt-2">
							<div className="group-icon-text">
								<Phone className="icon-color" size={15} />
								<p className="label">Teléfono: </p>
							</div>
							<p className="text">{user?.phone ? user.phone: "---"}</p>
						</div>
					</div>
				</CardBody>
			</Card>
		</div>
	);
};
const mapDispatchToProps = bindAll({ ModalSliceActions : ModalSlice.actions, UsersSliceActions : UsersSlice.actions  });

export default connect(null, mapDispatchToProps)(UserCard);
