import { useEffect, useState } from "react";
import { connect }             from "react-redux";

//Our Importations
import { bindAll }      from "Helpers";
import { useDebounce }  from "Helpers/Hooks";
import GenericSerivices from "Store/Services/GenericSerivices";
import UsersPage        from "./UsersPage";

const UsersPageContainer = ({
	genericSerivices,
}) => {

	const [statesOptions, setStatesOptions] = useState([]);
	const [query, setQuery] = useState({
		status     : {},
		search     : "",
		orderBy    : "",
		roles      : [],
		states     : [],
		validities : [],
	});

	const handleSearch = useDebounce((search) => handleChangeFilter("search", search), 700);

	const handleChangeFilter = (filter, val) => setQuery(prev => ({ ...prev, [filter] : val }));

	const handleFilters = (page) => {
		const role       = query.roles.map(element =>  element?.value);
		const state      = query.states.map(element =>  element?.value);
		const validation = query.validities.map(element =>  element?.value);

		let order;

		if (query.orderBy) {
			order = -1;
		}

		genericSerivices.getAll({
			module     : "users",
			actionType : "getAllUsers",
			query      : {
				page,
				role,
				order,
				state,
				validation,
				search  : query.search,
				orderBy : query.orderBy?.value,
				active  : query.status?.value,
			},
		});
	};

	useEffect(() => {
		handleFilters();
	}, [query]);

	useEffect(() => {
		const callback = ({ data }) => {
			const states = data.data.map(state =>  ({ label : state.name, value : state._id}) );

			setStatesOptions(states);
		};

		genericSerivices.getAll({ module : "places", callback });
		return () => {
			setStatesOptions([]);
		};
	}, []);


	return (
		<UsersPage
			delegations={{
				handleSearch,
				handleFilters,
				statesOptions,
				handleChangeFilter,
			}}
		/>
	);
};
const mapDispatchToProps = bindAll({ GenericSerivices });

export default connect(null, mapDispatchToProps)(UsersPageContainer);
